/* global jQuery */
/*eslint no-undef: "warn"*/
void (function start($) {
	'use strict';

	$('.downloadBtn').click(() => {
		const countries = $('div.countriesContainer:first').find('.countries');
		const data = $('div.countriesContainer:eq(1)').find('.countries');
		const years = $('div.countriesContainer:last').find('.countries');
		let trackingUrl = '';

		function updateTrackingUrl(elements, name) {
			elements.each((index, element) => {
				const begDelimiter = trackingUrl.length > 0 ? '&' : '?';
				const delimiter = index === 0 ? `${begDelimiter + name}=` : ',';
				trackingUrl +=
					delimiter +
					element.innerText.substring(
						0,
						element.innerText.length - 1
					);
			});
		}

		updateTrackingUrl(countries, 'countries');
		updateTrackingUrl(data, 'data');
		updateTrackingUrl(years, 'years');

		cdAnalytics.trackLink(
			`https://www.iif.com/Research/Data${trackingUrl}`,
			'Economic Data'
		);
	});
})(jQuery);
